import { useWeb3React } from "@web3-react/core";
import { Web3Provider } from "@ethersproject/providers";
import Style from "./bridge.module.css";
import { networks, setNetwork } from "../utils/EthereumUtils";
import { useMemo } from "react";

export default function PancakeContainer() {
  return (
    <div className="row justify-content-evenly">
      <div className={"col col-md-auto mb-2 " + Style.cont1}>
        <div className="row justify-content-md-center mb-4">
          <h3 className="text-center text-white">Buy WILG on PancakeSwap</h3>
        </div>
        <Pancake />
      </div>
    </div>
  );
}

function Pancake() {
  const { chainId, library } = useWeb3React<Web3Provider>();
  if (!chainId || !library) {
    throw new Error();
  }
  const bnbSelected = useMemo(() => chainId === parseInt(networks.bnb.chainId, 16), [chainId]);
  return (
    <>
      {!bnbSelected && (
        <div className="row justify-content-md-center m-3 mb-4">
          <div
            style={{ backgroundColor: "#272738", padding: "15px", borderRadius: "10px" }}
            className="col col-md-auto text-center"
          >
            <button
              onClick={() => setNetwork(library, networks.bnb)}
              style={{ color: "white" }}
              className="btn btn-lg btn-outline-secondary mb-3"
            >
              Switch to Binance network
            </button>
            <h6 className="text-center text-white">
              Please switch to Binance Network to be able to use PancakeSwap.
            </h6>
          </div>
        </div>
      )}
      <div
        className="row justify-content-md-center m-3"
        style={{ display: bnbSelected ? undefined : "none" }}
      >
        <div className="col text-center">
          <iframe
            title="poocoin"
            src="https://poocoin.app/embed-swap?outputCurrency=0x8c01dFbCBCa328F3B379957BD93B992388EdcbEB"
            width="420"
            height="600"
          ></iframe>
        </div>
      </div>
    </>
  );
}
