import { useEffect, useState } from "react";
import { UnsupportedChainIdError, useWeb3React, Web3ReactProvider } from "@web3-react/core";
import {
  InjectedConnector,
  NoEthereumProviderError,
  UserRejectedRequestError,
} from "@web3-react/injected-connector";
import { Web3Provider } from "@ethersproject/providers";

import { ExternalProvider, JsonRpcFetchFunc } from "@ethersproject/providers/src.ts/web3-provider";
import Onboarding from "@metamask/onboarding";
import { BrowserRouter as Router, Switch, Route, Redirect, NavLink } from "react-router-dom";
import Bridge from "./bridge/Bridge";
import Pancake from "./pancake/Pancake";
import TokenCreator from "./token_creator/connected";
import Coins from "./images/coins.png";
import Crosschain from "./images/crosschain.svg";
import Pancakeimg from "./images/pancakeswap.svg";
import Style from "./app.module.css";

function RouterComponent() {
  const [sidebarOpen, setSidebarOpen] = useState(false);

  return (
    <Router>
      <div
        style={{ backgroundColor: "black" }}
        className={sidebarOpen ? Style["sb-sidenav-toggled"] : ""}
      >
        <div className="d-flex" id={Style["wrapper"]}>
          {/* Sidebar*/}
          <div className="border-end" id={Style["sidebar-wrapper"]}>
            <div className={Style["sidebar-heading"] + " border-bottom"}>Menu</div>
            <div className={Style["list-group"] + "list-group-flush"}>
              <NavLink
                to="/token-creator"
                style={{ textDecoration: "none" }}
                className={Style.nav_hover}
              >
                <div className={Style.nav_1}>
                  <div>
                    <img src={Coins} width="40" className={Style.logo} alt="" />
                  </div>
                  <div className={Style.side_text}> Token Creator </div>
                </div>
              </NavLink>

              <NavLink to="/bridge" style={{ textDecoration: "none" }} className={Style.nav_hover}>
                <div className={Style.nav_1}>
                  <div>
                    <img src={Crosschain} width="40" className={Style.logo} alt="" />
                  </div>
                  <div className={Style.side_text}> Bridge </div>
                </div>
              </NavLink>
              <NavLink to="/pancake" style={{ textDecoration: "none" }} className={Style.nav_hover}>
                <div className={Style.nav_1}>
                  <div>
                    <img src={Pancakeimg} width="40" className={Style.logo} alt="" />
                  </div>
                  <div className={Style.side_text}> Pancake </div>
                </div>
              </NavLink>
            </div>
          </div>
          {/* Page content wrapper*/}
          <div id={Style["page-content-wrapper"]}>
            {/* Top navigation*/}
            <nav className="navbar navbar-expand-lg navbar-dark">
              <div className="container-fluid">
                <button
                  style={{ backgroundColor: "#383848" }}
                  className={"btn " + Style.togglebtn}
                  onClick={() => setSidebarOpen(!sidebarOpen)}
                  id="sidebarToggle"
                >
                  <span className="navbar-toggler-icon" />
                </button>
              </div>
            </nav>
            {/* Page content*/}
            <div className="container-fluid">
              <Switch>
                <Route path="/pancake">
                  <InjectedLogin>
                    <Pancake />
                  </InjectedLogin>
                </Route>
                <Route path="/bridge">
                  <InjectedLogin>
                    <Bridge />
                  </InjectedLogin>
                </Route>
                <Route path="/token-creator">
                  <InjectedLogin>
                    <TokenCreator />
                  </InjectedLogin>
                </Route>
                <Route>
                  <Redirect to="/bridge" />
                </Route>
              </Switch>
            </div>
          </div>
        </div>
      </div>
    </Router>
  );
}

function getErrorMessage(error: Error) {
  if (error instanceof NoEthereumProviderError) {
    return "No Ethereum browser extension detected, install MetaMask on desktop or visit from a dApp browser on mobile.";
  } else if (error instanceof UnsupportedChainIdError) {
    return "You're connected to an unsupported network.";
  } else if (error instanceof UserRejectedRequestError) {
    return "Please authorize this website to access your Ethereum account.";
  } else {
    console.error(error);
    return "An unknown error occurred. Check the console for more details.";
  }
}

function getLibrary(provider: ExternalProvider | JsonRpcFetchFunc): Web3Provider {
  const library = new Web3Provider(provider);
  library.pollingInterval = 12000;
  return library;
}

export default function App() {
  const [unknownErrors, setUnknownErrors] = useState<string[]>([]);
  useEffect(() => {
    const addError = (e: string) => setUnknownErrors(es => (es.includes(e) ? es : [...es, e]));
    window.addEventListener("unhandledrejection", ({ reason }: PromiseRejectionEvent) => {
      // web3-provider-engine causes it and couldn't find why
      if (reason.message !== "Callback was already called.") {
        console.log(reason);
        addError(reason?.data?.message ?? reason.message);
      }
    });
    window.onerror = (_, __, ___, ____, error) => {
      if (error) {
        addError(error.message);
      }
      return false;
    };
  }, []);
  return (
    <>
      <Web3ReactProvider getLibrary={getLibrary}>
        {unknownErrors.map(e => {
          const remove = () => setUnknownErrors(es => es.filter(_e => _e !== e));
          return (
            <div
              onClick={remove}
              style={{
                cursor: "pointer",
                display: "flex",
                justifyContent: "space-between",
                position: "absolute",
                top: "20px",
                right: "20px",
              }}
              className="alert alert-danger alert-dismissible fade show"
              role="alert"
            >
              <div style={{ marginRight: "20px" }}>{e}</div>
              <div>
                <button
                  style={{
                    border: "none",
                    cursor: "pointer",
                    backgroundColor: "#f8d7da",
                    marginRight: "-30px",
                    width: "30px",
                  }}
                  type="button"
                  className="close"
                  data-dismiss="alert"
                  aria-label="Close"
                >
                  <span style={{ height: "40px" }} aria-hidden="true">
                    &times;
                  </span>
                </button>
              </div>
            </div>
          );
        })}
        <RouterComponent />
      </Web3ReactProvider>
    </>
  );
}

function InjectedLogin({ children }: { children: JSX.Element }) {
  const { active, error, activate } = useWeb3React<Web3Provider>();
  useEffect(() => {
    activate(new InjectedConnector({})).then();
  }, [activate]);
  return active ? (
    <>{children}</>
  ) : error instanceof NoEthereumProviderError ? (
    <InstallMetaMaskButton />
  ) : (
    <>
      <div style={{ margin: "400px", color: "#0d6efd", borderColor: "#0d6efd" }}>
        {error && getErrorMessage(error)}
      </div>
    </>
  );
}

function InstallMetaMaskButton() {
  const [onboarding, setOnboarding] = useState(false);
  return onboarding ? (
    <button disabled className="btn btn-outline-primary">
      Onboarding in progress
    </button>
  ) : (
    <div className="row" style={{ width: "300px", margin: "auto" }}>
      <div className="col align-self-center">
        <button
          style={{ marginTop: "400px" }}
          className="btn btn-outline-primary"
          onClick={() => {
            setOnboarding(true);
            new Onboarding().startOnboarding();
          }}
        >
          Install MetaMask
        </button>
      </div>
    </div>
  );
}
